body {
  margin: 0;
  font-family: "Trebuchet MS", "Regular", serif;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
  background-color: #ffffff;
}
.visually-hidden:not(:focus):not(active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: #666666;
}
ul {
  list-style: none;
}
.stats {
  display: block;
  height: 480px;
  width: 320px;
  background-color: #ffffff;
  border-radius: 12px;
  position: relative;
  margin-top: 29px;
  overflow: hidden;
}
.stats:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 312px;
  height: 472px;
  border: 4px solid #1698d9;
  border-radius: 12px;
  z-index: 111;
}
.stats__wrapper {
  position: absolute;
  margin-left: 47px;
}
.stats-weight {
  color: #ffffff;
  width: 78px;
  height: 78px;
  position: absolute;
  bottom: 17px;
  right: 16px;
  border-radius: 50%;
  background-color: #1698d9;
  transition: 0.3s;
  transform: scale(1);
}
.checkbox-other:hover .main__fone:before {
  border-right: 4px solid #2ea8e6;
  transition: 0.3s;
}
.checkbox-other:hover .stats-weight {
  background-color: #2ea8e6;
  transition: 0.3s;
  transform: scale(1);
}
.stats:hover:before {
  border: 4px solid #2ea8e6;
  transition: 0.3s;
  transform: scale(1);
}
.stats-picture {
  position: absolute;
  bottom: -88px;
  left: -26px;
}
.stats-weight__count {
  margin: 0;
  margin-top: 20px;
  margin-left: 26px;
  font-size: 42px;
  line-height: 22px;
}
.stats-weight__count--zero {
  margin-left: 6px;
}
.stats-weight__massa {
  margin-top: 8px;
  margin-left: 30px;
  font-size: 21px;
  line-height: 22px;
}
.stats-info {
  margin-top: 24px;
}
.stats-info__description {
  font-family: "Myriad Pro", "Regular", serif;
  font-size: 16px;
  margin-top: 19px;
}
.stats-info__title {
  color: #000000;
  font-size: 48px;
  margin-top: 25px;
  margin-bottom: 0;
}
.stats-info__teste {
  color: #000000;
  font-weight: bold;
  font-size: 24px;
  line-height: 26px;
  margin-top: 19px;
  margin-bottom: 0;
}
.stats-info__table {
  width: 190px;
  margin-top: 16px;
}
.stats-info__table--present {
  display: block;
  margin-top: 0;
}
.main {
  min-width: 360px;
}
.main__wrapper {
  background: url(../img/pattern.png);
  background-size: 100%;
  box-shadow: inset 0 -140px 50px -30px rgba(0, 0, 0, 0.5), inset 0 168px 60px -30px rgba(0, 0, 0, 0.5);
}
.main__list {
  display: flex;
  justify-content: center;
}
.main__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 320px;
}
.main__block {
  position: relative;
}
.main__item {
  background-color: #ffffff;
  width: 320px;
  height: 480px;
  border-radius: 12px;
  margin-bottom: 30px;
}
.main__fone:before {
  border-right: 4px solid #1698d9;
  width: 40px;
  height: 62px;
  content: '';
  position: absolute;
  transform: rotate(45deg);
  left: -13px;
  top: 7px;
  background: url(../img/pattern.png);
  background-position: 14px 21px;
  z-index: 1111;
}
label input[type="checkbox"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}
label input[type="checkbox"]:checked ~ .stats:before {
  border: 4px solid #d91667;
}
label input[type="checkbox"]:checked:hover ~ .stats:before {
  border: 4px solid #e62e7a;
}
label input[type="checkbox"]:checked ~ .main__fone:before {
  border-right: 4px solid #d91667;
}
label input[type="checkbox"]:checked:hover ~ .main__fone:before {
  border-right: 4px solid #e62e7a;
}
label input[type="checkbox"]:checked ~ .stats .stats-weight {
  background-color: #d91667;
}
label input[type="checkbox"]:checked:hover ~ .stats .stats-weight {
  background-color: #e62e7a;
}
.main__sentense--sentense {
  display: block;
}
.main__sentense--have,
.main__sentense--lose {
  display: none;
}
label input[type="checkbox"]:checked ~ .main__sentense--sentense {
  display: none;
}
label input[type="checkbox"]:checked ~ .main__sentense--lose {
  display: none;
}
label input[type="checkbox"]:checked ~ .main__sentense--have {
  display: block;
}
label input[type="checkbox"]:disabled ~ .main__sentense--sentense {
  display: none;
}
label input[type="checkbox"]:disabled ~ .main__sentense--have {
  display: none;
}
label input[type="checkbox"]:disabled ~ .main__sentense--lose {
  display: block;
}
label input[type="checkbox"]:disabled ~ .stats {
  opacity: 0.2;
}
label input[type="checkbox"]:disabled ~ .main__fone:before {
  border-right-color: #c1c1c1;
}
label input[type="checkbox"]:disabled ~ .stats:before {
  border-color: #000000;
}
label input[type="checkbox"]:disabled ~ .stats .stats-weight {
  background-color: #000000;
}
.main__title {
  font-family: "PT sans", "regular", serif;
  margin: 0px 15px 0 15px;
  font-size: 36px;
  line-height: 32px;
  color: #ffffff;
  text-align: center;
}
.main__sentense {
  font-family: "PT Sans", "Regular", serif;
  color: #ffffff;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
}
.main__sentense--buy {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer;
}
.main__sentense--lose {
  color: #ffff66;
}
@media (min-width: 820px) {
  .main__container {
    width: 800px;
  }
}
@media (min-width: 1200px) {
  .main__wrapper {
    height: 977px;
  }
  .main__container {
    width: 1120px;
    justify-content: space-between;
  }
  .main__title {
    padding-top: 107px;
  }
}
@font-face {
  font-weight: bold;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TrebuchetMS-Bold.woff") format("woff");
}
@font-face {
  font-weight: normal;
  font-family: "Myriad Pro";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/MyriadPro-Regular.woff") format("woff");
}
